import React, { Component } from 'react';

import Input from './Input';
import List from './List';

class TodoList extends Component{

    constructor(){
        super();
        this.state = {
            inpValue:'',//input框里的输入值
            todos:[
                {title:'1',done:false},
                {title:'2',done:true},
                {title:'3',done:false},
                {title:'4',done:false},
            ]//每一个li的值
        }
    }

    //本地存储功能实现
    componentDidMount(){
        
        let todos = localStorage.getItem('todos');
        if(todos){
            this.setState({
                todos:JSON.parse(todos)
            })
        }
        
    }

    componentDidUpdate(){
        localStorage.setItem(
            'todos',
            JSON.stringify(this.state.todos)
        )
    }

    handleChange = (e) => {
        this.setState({
            inpValue:e.target.value
        });    
    }

    deleteTodo=(idx)=>{
        this.setState({
              todos:this.state.todos.filter((item,index)=>index!=idx)
        });
        localStorage.setItem(
            'todo',
            JSON.stringify(this.state.todos)
        )
    }

    addTodo = (e) => {
        if(this.state.inpValue === ''){
            alert('项目不能为空！');
            return;
        }
        this.setState({
            todos:[
                {
                    title:this.state.inpValue,
                    done:false
                },
                ...this.state.todos
            ]
        })
        //添加完毕清空input框
        this.state.inpValue='';
        let inp = document.getElementById('inp');
        inp.value = '';
    }


    toggle = (index) => {
        let newTodos = JSON.parse(JSON.stringify(this.state.todos))
        newTodos[index].done = !newTodos[index].done;
        this.setState({
           todos:newTodos
        })
    }

    render() {
        let todos = this.state.todos;
        let inpValue = this.state.inpValue;
        return (
            <div className='box'>
                <h1>TodoList</h1>
                <Input
                value={inpValue}
                onHandleChange={this.handleChange} 
                onAddTodo={this.addTodo} />
                <List 
                todos={todos}
                onDeleteTodo={this.deleteTodo} 
                onToggle={this.toggle} />
            </div>
        )
    }
    
}

export default TodoList
